.QuizCard {
  height: 100%;
  padding-top: 70px;

  @media screen and (max-width: 1025px) {
    padding-top: 60px;
  }

  @media screen and (max-width: 375px) {
    height: 100%;
    overflow-y: scroll;
  }

  .container {
    height: 100%;
  }

  &.firstScreen {
    .QuizCard__text {
      animation: opacity 1s 5.5s ease forwards;
    }

    .QuizCard__options {
      animation: bounce 1s ease 8.5s forwards;
    }

    .QuizCard__image {
      animation: bounce 1.5s ease 7s forwards;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    height: 100%;

    @media screen and (max-width: 1025px) {
      flex-direction: column;
      align-items: center;
      height: 95%;
    }
  }

  &__text {
    width: 100%;
    max-width: 580px;
    opacity: 0;
    visibility: hidden;

    &.animate {
      animation: opacity 0.9s ease forwards;
    }

    @media screen and (max-width: 1025px) {
      max-width: 100%;
      text-align: center;
      margin-bottom: -90px;
    }

    @media screen and (max-width: 480px) {
      text-align: center;
    }
  }

  &__title {
    margin-bottom: 34px;
    font-family: "Grtsk Giga";
    font-size: 54px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -0.03em;

    @media screen and (max-width: 1025px) {
      margin-bottom: 16px;
      font-size: 40px;
    }

    @media screen and (max-width: 768px) {
      font-size: 32px;
    }

    @media screen and (max-width: 400px) {
      font-size: 25px;
    }

    @media screen and (max-height: 800px) {
      font-size: 25px;
    }
  }

  &__tip {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 16px;
    text-transform: uppercase;
    font-family: "Barlow";

    @media screen and (max-width: 1025px) {
      font-size: 12px;
      justify-content: center;
    }

    &:before {
      content: "";
      width: 12px;
      height: 12px;
      display: inline-block;
      vertical-align: middle;
      background: url("../../images/star2.svg");
    }
  }

  &__options {
    display: flex;
    width: 100%;
    max-width: 678px;
    max-height: 720px;
    height: 100%;
    margin-top: -125px;
    gap: 10px;
    position: relative;
    z-index: 1;
    transform: translateY(120vh);

    &.animate {
      animation: bounce 1s ease forwards;
    }

    @media screen and (max-width: 1025px) {
      flex-direction: column;
      justify-content: flex-end;
      max-width: 100%;
      margin-top: auto;
    }
  }

  &__option {
    flex: 1 0 calc(50% - 10px);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 40px;
    background: #f7f7f7;
    font-family: "Grtsk Zetta";
    font-size: 32px;
    font-weight: 700;
    text-align: center;
    letter-spacing: -0.03em;
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    border-radius: 5px;
    border: 1px solid #dddddd;
    transition: clip-path 2s ease;

    @media screen and (min-width: 1025px) {
      clip-path: path(
        "M5 0H329C332.866 0 336 3.13401 336 7V713C336 716.866 332.866 720 329 720H5C1.13401 720 0 716.866 0 713V7C0 3.13401 1.13401 0 5 0Z"
      );
    }

    @media screen and (max-width: 1025px) {
      height: 300px;
      flex-basis: auto;
      border-radius: 5px;
      font-size: 18px;
    }

    @media screen and (max-width: 768px) {
      height: 150px;
      flex-grow: 0;
    }

    // &::after {
    //   content: "";
    //   position: absolute;
    //   left: 0;
    //   top: 0;
    //   width: 100%;
    //   height: 100%;
    //   z-index: -1;
    //   display: block;
    //   background: center / 100% no-repeat url("../../images/green_hover.svg");
    // }

    @media screen and (min-width: 1025px) {
      &:hover {
        background-color: #8bf62a;
        border: transparent;
        clip-path: url(#svgClip);
      }
    }
  }

  &__image {
    width: 300px;
    height: 300px;
    margin-left: auto;
    transform: translateY(120vh);

    &.animate {
      animation: bounce 1s ease 1s forwards;
    }

    @media screen and (max-width: 1025px) {
      margin: 40px auto 0;
      height: 270px;
    }

    @media screen and (max-width: 375px) {
      width: 220px;
      height: 220px;
      margin: 10px auto 0;
    }

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: contain;
    }
  }
}

@keyframes bounce {
  0% {
    transform: translateY(120%);
  }

  25% {
    transform: translateY(-10%);
  }

  50% {
    transform: translateY(10%);
  }

  75% {
    transform: translateY(-1%);
  }

  85% {
    transform: translateY(5%);
  }

  90% {
    transform: translateY(3%);
  }

  100% {
    transform: translateY(0%);
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
    visibility: hidden;
  }

  100% {
    opacity: 1;
    visibility: visible;
  }
}
