.QuizAnswer {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 99;
  padding: 40px;
  opacity: 0;
  background: center / cover no-repeat url("../../images/background.png") #fff;
  transition: opacity 0.5s ease-in-out;

  @media screen and (max-width: 1025px) {
    top: 80px;
    padding: 20px;
    background: none;
  }

  &--visible {
    opacity: 1;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    gap: 70px;
    padding: 80px;
    max-height: 720px;
    height: 100%;
    background-color: #8bf62a;

    @media screen and (max-height: 700px) {
      padding: 40px 80px;
      overflow-y: visible;
      overflow-x: clip;
    }

    @media screen and (max-width: 1025px) {
      height: 100%;
      max-height: 780px;
      padding: 80px 40px;
      border-radius: 5px;
      min-height: 640px;
    }

    @media screen and (max-width: 375px) {
      overflow-y: scroll;
      overflow-x: hidden;
      max-height: 500px;
      min-height: unset;
    }
  }

  &__title {
    max-width: 600px;
    margin: 0 auto;
    font-family: "Grtsk Zetta";
    font-size: 69px;
    font-weight: 700;
    color: #000;
    letter-spacing: -0.03em;
    text-align: center;
    text-transform: uppercase;

    @media screen and (max-height: 700px) {
      font-size: 40px;
    }

    @media screen and (max-width: 1025px) {
      font-size: 32px;
    }

    @media screen and (max-width: 480px) {
      max-width: 270px;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media screen and (max-width: 1025px) {
      flex-direction: column;
    }
  }

  &__animation {
    width: 100%;
    max-width: 320px;
    position: relative;
    height: 200px;
    transform: translateY(100vh);
    z-index: 3;

    &.animate {
      animation: animUp 1s ease 0.5s forwards;
    }

    > div {
      position: absolute;
      left: -38%;
      top: -390px;
      width: 100vw;
      z-index: 99;
      pointer-events: none;

      @media screen and (min-width: 1600px) {
        top: -490px;
      }

      @media screen and (max-width: 1280px) {
        left: -60%;
      }

      @media screen and (max-width: 1025px) {
        top: -23em;
        width: 30em;
        left: 0;
      }

      @media screen and (max-width: 480px) {
        top: -18em;
        width: 25em;
        left: 0;
      }
    }
  }

  &__next {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -100px;
    right: -65px;
    z-index: 1;

    @media screen and (max-width: 1025px) {
      right: 6px;
      bottom: -75px;
    }
  }

  &__tip {
    position: relative;
    width: 100%;
    max-width: 730px;
    z-index: 1;
    transform: translateY(100vh) rotate(-6deg);

    &.animate {
      animation: up 1s ease 0.5s forwards;
    }

    &-wrapper {
      background-color: #fff;
      padding: 50px 50px 70px;

      @media screen and (max-width: 1025px) {
        min-height: 250px;
        padding: 30px 20px 100px;
      }

      @media screen and (max-width: 375px) {
        padding: 30px 20px 50px;
      }
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      right: -5px;
      bottom: -5px;
      width: 100%;
      height: 100%;
      background-color: #000;
      z-index: -1;

      @media screen and (max-width: 1025px) {
        right: -10px;
        bottom: -10px;
      }
    }

    h3 {
      margin-bottom: 10px;
      text-transform: uppercase;
      font-size: 18px;
      font-family: "Barlow";

      @media screen and (max-width: 1025px) {
        font-size: 14px;
      }
    }

    p {
      font-family: "Grtsk Zetta";
      font-size: 32px;
      letter-spacing: -0.003em;
      font-weight: 300;

      @media screen and (max-height: 700px) {
        font-size: 20px;
      }

      @media screen and (max-width: 1025px) {
        font-size: 20px;
      }
    }
  }

  &__back {
    position: absolute;
    top: 10px;
    left: -15px;
  }
}

@keyframes up {
  0% {
    transform: translateY(100vh) rotate(-6deg);
  }

  100% {
    transform: translateY(0%) rotate(-6deg);
  }
}

@keyframes animUp {
  0% {
    transform: translateY(100vh);
  }

  100% {
    transform: translateY(0%);
  }
}
