.draggable-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  &.animate {
    pointer-events: none;
  }
}

.draggable-item {
  width: 300px;
  height: 300px;

  @media screen and (max-width: 1025px) {
    width: 150px;
    height: 150px;
  }

  &.animate {
    $animationCount: 11;

    @for $i from 1 through $animationCount {
      $animationName: "bounceElems-#{$i}";

      &.animation-#{$i} {
        animation: #{$animationName} 2.5s forwards ease-in-out 1s,
          roll 4s forwards ease-in-out 2.5s;
      }
    }
  }

  .img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .img-border {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease;
  }

  &.dragging {
    .img {
      opacity: 0;
      visibility: hidden;
    }
    .img-border {
      opacity: 1;
      visibility: visible;
    }
  }
}

