.QuizFinish {
  height: 100%;
  padding-top: 40px;
  position: relative;
  background-color: #fff;

  &__arrow {
    position: absolute;
    max-width: 70px;
    max-height: 60px;
    z-index: 9;

    @media screen and (max-width: 1025px) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &.high {
    .QuizFinish__arrow {
      left: 25%;
      bottom: 35%;
    }
  }

  &.medium {
    .QuizFinish__arrow {
      left: 40%;
      bottom: 70%;
    }
  }

  &.low {
    .QuizFinish__arrow {
      max-width: 50px;
      max-height: 50px;
      left: 65%;
      top: 0%;
    }
  }

  &__background {
    height: 100%;
    max-width: 60%;
    position: absolute;
    right: 0;
    top: -100px;
    z-index: 2;

    &-mob {
      height: 100%;
      height: 100%;
      max-height: 900px;
      max-width: 100%;
      position: absolute;
      left: 0;
      bottom: -270px;
      z-index: 3;

      @media screen and (min-width: 1025px) {
        display: none;
      }
    }

    @media screen and (max-width: 1025px) {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }

  .marquee-container {
    position: relative;
    z-index: 1;
    border-top: 2px solid #000;
    border-bottom: 2px solid #000;
    padding: 21px 0 25px;
    background-color: #efff36;

    &.mobile {
      display: none !important;
    }

    &.desktop {
      @media screen and (max-width: 1025px) {
        display: none !important;
      }
    }

    &.mobile {
      @media screen and (max-width: 1025px) {
        display: flex !important;
        position: relative;
        z-index: 1;
        width: 105vw;
        margin: 35px -20px 0;
        padding: 30px 0;
      }

      @media screen and (max-width: 768px) {
        padding: 15px 0;
      }
    }

    .child {
      display: flex;
      align-items: center;
      font-family: "Grtsk Zetta";
      font-size: 32px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: -0.03em;

      @media screen and (max-width: 1025px) {
        font-size: 24px;
      }

      &::after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 6px;
        height: 6px;
        margin-left: 24px;
        margin-right: 35px;
        background-color: #000;
        border-radius: 50%;
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    position: relative;
    height: 100%;
    z-index: 3;

    @media screen and (max-width: 1025px) {
      height: 100%;
      flex-direction: column;
    }
  }

  &__content {
    position: relative;
    padding-top: 40px;
    padding-left: 40px;

    @media screen and (max-width: 1025px) {
      padding-top: 0;
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  &__title {
    max-width: 630px;
    margin-bottom: 40px;
    font-size: 80px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -0.03em;

    @media screen and (max-width: 1025px) {
      font-size: 50px;
      max-width: 100%;
      margin-bottom: 15px;

      span {
        font-family: "Grtsk Zetta";
        font-weight: 300;
      }
    }

    @media screen and (max-width: 768px) {
      font-size: 32px;
    }
  }

  &__subtitle,
  &__text {
    max-width: 630px;
    font-family: "Grtsk Zetta";
    font-size: 24px;
    font-weight: 300;

    @media screen and (max-width: 1025px) {
      max-width: 100%;
      font-size: 24px;
    }

    @media screen and (max-width: 768px) {
      font-size: 18px;
    }
  }

  &__subtitle {
    margin-bottom: 20px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;

    &-wrapper {
      position: absolute;
      bottom: 14em;
      left: 50em;
      transform: translateX(-50%);
      width: 100%;
      max-width: 460px;
      z-index: 4;

      @media screen and (max-width: 1200px) {
        right: 150px;
        left: unset;
        transform: unset;
      }

      &.bottom {
        bottom: 16em;

        @media screen and (max-width: 1025px) {
          bottom: 10px;
        }
      }

      @media screen and (max-width: 1025px) {
        max-width: 400px;
        left: 63%;
        right: unset;
        bottom: 40px;
        transform: translateX(-50%);
      }

      @media screen and (max-width: 400px) and (max-height: 700px) {
        bottom: -300px;
      }
    }

    &-tip {
      position: absolute;
      z-index: -1;
      right: -147px;
      top: -62px;
      transform: rotate(12deg);
      width: 107%;
      padding: 35px 24px;
      max-width: 480px;
      height: 120px;
      font-family: "Grtsk Zetta";
      font-size: 21px;
      font-weight: 300;
      text-align: center;
      color: #fff;
      background: center / contain no-repeat url("../../images/btn_tip.png");
      text-transform: uppercase;

      @media screen and (max-width: 1025px) {
        max-width: 350px;
        font-size: 16px;
        right: 70px;
        transform: rotate(8deg);
        background: center / contain no-repeat
          url("../../images/btn_tip_mob.png");
      }
    }
  }

  &__image {
    width: 100%;
    max-width: 50%;
    margin-top: -150px;
    height: 100%;

    @media screen and (max-width: 1025px) {
      margin-top: unset;
      max-width: 100%;
      position: relative;
      z-index: 3;
      margin-bottom: -100px;
    }

    @media screen and (max-width: 768px) {
      margin-bottom: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: bottom right;
    }
  }

  &__decor {
    position: absolute;

    @media screen and (max-width: 1025px) {
      display: none;
    }

    img {
      display: none;
    }

    &.low {
      bottom: 100px;
      left: 150px;

      .juice {
        display: block;
        transform: scale(-1) rotate(33deg);
        max-width: 300px;
      }
    }

    &.medium {
      bottom: 50px;
      left: 150px;

      .box {
        max-width: 350px;
        transform: rotate(-30deg);
        display: block;
      }
    }

    &.high {
      bottom: -50px;
      left: -40px;

      .abstract {
        display: block;
        max-width: 400px;
        transform: rotate(110deg);
      }
    }
  }
}
